import React, { Component } from 'react';
import 'react-beautiful-dnd'
import './App.css';

class App extends Component {
  render() {
    var inFields;
    if ("Alteryx" in window) {
        inFields = window.Alteryx.Gui.Manager.getIncomingFields()
    } else {
        // If we aren't running in Alteryx, use some sample data
        inFields = [
            {strName: "Id"},
            {strName: "Value"},
            {strName: "Category"},
            {strName: "Created On"},
            {strName: "Group1"},
            {strName: "Group2"},
            {strName: "Group3"},
        ];
    }

    var displaySize = Math.min(20,inFields.length);
    var listItems = inFields.map((item) => <option>{item.strName}</option>);

    return (
      <div className="App">
        <select multiple="multiple" size={displaySize}>{listItems}</select>
      </div>
    );
  }
}

export default App;



