import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';


window.Alteryx.Gui.SetConfiguration = function(currentToolConfiguration) {
    window.Alteryx.JsEvent(JSON.stringify({Event: 'SetConfiguration'}));
};

window.Alteryx.Gui.AfterLoad = function(manager, AlteryxDataItems, json) {
    ReactDOM.render(<App />, document.getElementById('root'));
};

window.Alteryx.Gui.GetConfiguration = function() {
    window.Alteryx.JsEvent(JSON.stringify({
        Event: 'GetConfiguration',
        Configuration: {
            SortedFields: []
        },
        Annotation: ''
    }))
};



